'use client'

import { ReactNode } from 'react'

interface EditorLayoutProps {
  leftPanel?: ReactNode
  rightPanel?: ReactNode
  topToolbar?: ReactNode
  children: ReactNode
}

export default function EditorLayout({
  leftPanel,
  rightPanel,
  topToolbar,
  children,
}: EditorLayoutProps) {
  return (
    <div className="h-screen flex flex-col bg-bg-primary text-text-primary">
      {/* Top Toolbar */}
      {topToolbar && (
        <div className="h-14 border-b border-border-color bg-bg-secondary px-4 flex items-center">
          {topToolbar}
        </div>
      )}

      {/* Main Content Area */}
      <div className="flex-1 flex overflow-hidden">
        {/* Left Panel */}
        {leftPanel && (
          <div className="w-80 border-r border-border-color bg-bg-secondary overflow-y-auto">
            {leftPanel}
          </div>
        )}

        {/* Canvas Area */}
        <div className="flex-1 bg-bg-primary overflow-hidden">
          {children}
        </div>

        {/* Right Panel */}
        {rightPanel && (
          <div className="w-70 border-l border-border-color bg-bg-secondary overflow-y-auto">
            {rightPanel}
          </div>
        )}
      </div>
    </div>
  )
}
