'use client'

import { useCallback } from 'react'
import { Editor } from 'grapesjs'
import { useAIChatStore } from '@/store/aiChatStore'
import { injectHTMLToEditor } from '@/lib/ai/componentInjector'

export function useAIChat(editor?: Editor | null) {
  const { addMessage, setGenerating, selectedComponent } = useAIChatStore()

  const sendMessage = useCallback(
    async (message: string) => {
      // Add user message
      addMessage({ role: 'user', content: message })
      setGenerating(true)

      try {
        const response = await fetch('/api/ai/chat', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            messages: useAIChatStore.getState().messages.map((msg) => ({
              role: msg.role,
              content: msg.content,
            })),
            selectedComponent: selectedComponent
              ? {
                  id: selectedComponent.id,
                  type: selectedComponent.type,
                  name: selectedComponent.name,
                  html: selectedComponent.html,
                }
              : undefined,
          }),
        })

        if (!response.ok) {
          throw new Error(`API error: ${response.status}`)
        }

        const reader = response.body?.getReader()
        if (!reader) {
          throw new Error('No response body')
        }

        let accumulatedContent = ''
        const decoder = new TextDecoder()

        // Read the stream
        while (true) {
          const { done, value } = await reader.read()

          if (done) break

          const chunk = decoder.decode(value, { stream: true })
          const lines = chunk.split('\n')

          for (const line of lines) {
            if (line.startsWith('data: ')) {
              const data = line.slice(6)

              try {
                const parsed = JSON.parse(data)

                if (parsed.done) {
                  // Stream finished
                  break
                }

                if (parsed.content) {
                  accumulatedContent += parsed.content

                  // Update the last message (or create if first chunk)
                  const messages = useAIChatStore.getState().messages
                  const lastMessage = messages[messages.length - 1]

                  if (lastMessage && lastMessage.role === 'assistant') {
                    // Update existing assistant message
                    useAIChatStore.setState({
                      messages: [
                        ...messages.slice(0, -1),
                        { ...lastMessage, content: accumulatedContent },
                      ],
                    })
                  } else {
                    // Create new assistant message
                    addMessage({
                      role: 'assistant',
                      content: accumulatedContent,
                    })
                  }
                }
              } catch (e) {
                console.error('Error parsing SSE data:', e)
              }
            }
          }
        }

        setGenerating(false)

        // Try to inject HTML into the editor
        if (editor && accumulatedContent) {
          const injected = injectHTMLToEditor(
            editor,
            accumulatedContent,
            selectedComponent || undefined
          )

          if (injected) {
            console.log('✓ HTML successfully injected to canvas')
          } else {
            console.log('✗ No HTML found in response or injection failed')
          }
        }

        return accumulatedContent
      } catch (error) {
        console.error('Error sending message:', error)
        addMessage({
          role: 'assistant',
          content: 'Sorry, there was an error processing your request. Please make sure OPENROUTER_API_KEY is configured.',
        })
        setGenerating(false)
        throw error
      }
    },
    [addMessage, setGenerating, selectedComponent, editor]
  )

  return { sendMessage }
}
