'use client'

import { useEffect, useRef } from 'react'
import grapesjs, { Editor } from 'grapesjs'
import GjsEditor, { Canvas, AssetsProvider } from '@grapesjs/react'
import 'grapesjs/dist/css/grapes.min.css'

interface GrapesEditorProps {
  onReady?: (editor: Editor) => void
  onComponentSelect?: (component: any) => void
}

export default function GrapesEditor({
  onReady,
  onComponentSelect,
}: GrapesEditorProps) {
  const editorRef = useRef<Editor | null>(null)

  return (
    <div className="h-full w-full">
      <GjsEditor
        grapesjs={grapesjs}
        grapesjsCss="https://unpkg.com/grapesjs/dist/css/grapes.min.css"
        options={{
          height: '100%',
          width: '100%',
          storageManager: false,
          blockManager: {
            appendTo: '',
          },
          styleManager: {
            appendTo: '',
          },
          layerManager: {
            appendTo: '',
          },
          traitManager: {
            appendTo: '',
          },
          selectorManager: {
            appendTo: '',
          },
          panels: {
            defaults: [],
          },
          deviceManager: {
            devices: [
              { name: 'Desktop', width: '' },
              { name: 'Tablet', width: '768px', widthMedia: '992px' },
              { name: 'Mobile', width: '375px', widthMedia: '480px' },
            ],
          },
          canvas: {
            styles: [
              'https://cdn.jsdelivr.net/npm/tailwindcss@2.2.19/dist/tailwind.min.css',
            ],
          },
        }}
        onEditor={(editor: Editor) => {
          editorRef.current = editor

          // Add some default content
          editor.setComponents(`
            <div class="container mx-auto p-8">
              <h1 class="text-4xl font-bold mb-4">Welcome to GrapeJS AI Builder</h1>
              <p class="text-lg text-gray-600 mb-6">Start building your landing page with AI assistance</p>
              <button class="bg-purple-600 hover:bg-purple-700 text-white font-bold py-2 px-4 rounded">
                Get Started
              </button>
            </div>
          `)

          // Listen for component selection
          editor.on('component:selected', (component) => {
            onComponentSelect?.(component)
          })

          onReady?.(editor)
        }}
      >
        <AssetsProvider>
          <Canvas className="h-full w-full bg-white" />
        </AssetsProvider>
      </GjsEditor>
    </div>
  )
}
