'use client'

import { useCallback } from 'react'
import EditorLayout from '@/components/layout/EditorLayout'
import GrapesEditor from '@/components/editor/GrapesEditor'
import AIPanelLeft from '@/components/ai/AIPanelLeft'
import { useGrapesEditor } from '@/hooks/useGrapesEditor'
import { useAIChat } from '@/hooks/useAIChat'

export default function EditorPage() {
  const {
    editor,
    selectedComponent,
    handleEditorReady,
    handleComponentSelect,
  } = useGrapesEditor()

  const { sendMessage } = useAIChat(editor)

  const handleAIMessage = useCallback(
    async (message: string) => {
      try {
        await sendMessage(message)
      } catch (error) {
        console.error('Error in AI message handler:', error)
      }
    },
    [sendMessage]
  )

  return (
    <EditorLayout
      topToolbar={
        <div className="flex items-center justify-between w-full">
          <div className="flex items-center gap-4">
            <span className="font-semibold">GrapeJS AI Builder</span>
            {selectedComponent && (
              <span className="text-sm text-text-secondary">
                Selected: {selectedComponent.getName()}
              </span>
            )}
          </div>
          <div className="flex items-center gap-2">
            <button className="px-4 py-2 bg-accent-secondary text-black font-semibold rounded hover:bg-opacity-90">
              Publish
            </button>
          </div>
        </div>
      }
      leftPanel={
        <AIPanelLeft onSendMessage={handleAIMessage} />
      }
      rightPanel={
        <div className="p-4">
          <h2 className="text-lg font-semibold mb-4">Styles</h2>
          {selectedComponent ? (
            <div>
              <p className="text-sm text-text-secondary mb-2">
                Editing: {selectedComponent.getName()}
              </p>
              <p className="text-xs text-text-muted">
                Style properties will appear here
              </p>
            </div>
          ) : (
            <p className="text-text-secondary">Select an element to edit styles</p>
          )}
        </div>
      }
    >
      {/* GrapeJS Canvas */}
      <GrapesEditor
        onReady={handleEditorReady}
        onComponentSelect={handleComponentSelect}
      />
    </EditorLayout>
  )
}
