'use client'

import { useEffect, useRef } from 'react'
import { useAIChatStore } from '@/store/aiChatStore'
import ChatMessage from './ChatMessage'
import ChatInput from './ChatInput'
import SelectedElementBadge from './SelectedElementBadge'

interface AIPanelLeftProps {
  onSendMessage: (message: string) => void
}

export default function AIPanelLeft({ onSendMessage }: AIPanelLeftProps) {
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const { messages, isGenerating, selectedComponent, setSelectedComponent } =
    useAIChatStore()

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  return (
    <div className="h-full flex flex-col">
      {/* Header */}
      <div className="px-4 py-3 border-b border-border-color">
        <div className="flex items-center gap-2">
          <svg
            className="w-5 h-5 text-accent-secondary"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 3v4M3 5h4M6 17v4m-2-2h4m5-16l2.286 6.857L21 12l-5.714 2.143L13 21l-2.286-6.857L5 12l5.714-2.143L13 3z"
            />
          </svg>
          <h2 className="font-semibold text-lg">AI Assistant</h2>
        </div>
      </div>

      {/* Prompt */}
      <div className="px-4 py-4 border-b border-border-color">
        <p className="text-text-secondary text-sm">
          What would you like to build today?
        </p>
      </div>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto px-4 py-4">
        {selectedComponent && (
          <SelectedElementBadge
            component={selectedComponent}
            onClear={() => setSelectedComponent(null)}
          />
        )}

        {messages.length === 0 ? (
          <div className="text-center text-text-muted py-8">
            <p className="text-sm">No messages yet.</p>
            <p className="text-xs mt-1">
              Ask me to create a landing page, hero section, or modify selected
              elements.
            </p>
          </div>
        ) : (
          <>
            {messages.map((message) => (
              <ChatMessage key={message.id} message={message} />
            ))}
            {isGenerating && (
              <div className="flex justify-start mb-4">
                <div className="bg-bg-tertiary rounded-lg px-4 py-2 rounded-bl-sm">
                  <div className="flex items-center gap-2">
                    <div className="flex gap-1">
                      <div className="w-2 h-2 bg-accent-primary rounded-full animate-bounce" style={{ animationDelay: '0ms' }}></div>
                      <div className="w-2 h-2 bg-accent-primary rounded-full animate-bounce" style={{ animationDelay: '150ms' }}></div>
                      <div className="w-2 h-2 bg-accent-primary rounded-full animate-bounce" style={{ animationDelay: '300ms' }}></div>
                    </div>
                    <p className="text-sm text-text-secondary">AI is working...</p>
                  </div>
                </div>
              </div>
            )}
          </>
        )}
        <div ref={messagesEndRef} />
      </div>

      {/* Input Area */}
      <div className="px-4 py-4">
        <ChatInput
          onSubmit={onSendMessage}
          disabled={isGenerating}
          placeholder={
            selectedComponent
              ? `Modify ${selectedComponent.name}...`
              : 'Ask me to create...'
          }
        />
      </div>
    </div>
  )
}
