/**
 * Extract HTML from AI response
 * The AI should return pure HTML, but sometimes it might include markdown or explanations
 */
export function extractHTML(aiResponse: string): string | null {
  // Try to extract HTML content
  let html = aiResponse.trim()

  // Remove markdown code fences if present
  html = html.replace(/^```html?\n?/gm, '').replace(/^```\n?/gm, '')

  // Check if response contains HTML tags
  const hasHTMLTags = /<[a-z][\s\S]*>/i.test(html)

  if (!hasHTMLTags) {
    return null
  }

  return html
}

/**
 * Validate HTML structure
 */
export function isValidHTML(html: string): boolean {
  try {
    const parser = new DOMParser()
    const doc = parser.parseFromString(html, 'text/html')

    // Check for parser errors
    const parseErrors = doc.getElementsByTagName('parsererror')
    if (parseErrors.length > 0) {
      return false
    }

    return true
  } catch (error) {
    return false
  }
}

/**
 * Sanitize HTML to prevent XSS
 * Basic sanitization - in production, consider using a library like DOMPurify
 */
export function sanitizeHTML(html: string): string {
  // Remove potentially dangerous tags and attributes
  let sanitized = html

  // Remove script tags
  sanitized = sanitized.replace(/<script\b[^<]*(?:(?!<\/script>)<[^<]*)*<\/script>/gi, '')

  // Remove on* event handlers
  sanitized = sanitized.replace(/\s*on\w+\s*=\s*["'][^"']*["']/gi, '')
  sanitized = sanitized.replace(/\s*on\w+\s*=\s*[^\s>]*/gi, '')

  // Remove javascript: URLs
  sanitized = sanitized.replace(/href\s*=\s*["']javascript:[^"']*["']/gi, '')

  return sanitized
}
