import { Editor } from 'grapesjs'
import { extractHTML, isValidHTML, sanitizeHTML } from './htmlParser'

/**
 * Inject AI-generated HTML into the GrapeJS editor
 */
export function injectHTMLToEditor(
  editor: Editor | null,
  aiResponse: string,
  selectedComponent?: any
): boolean {
  if (!editor) {
    console.error('Editor is not initialized')
    return false
  }

  // Extract HTML from AI response
  const html = extractHTML(aiResponse)

  if (!html) {
    console.error('No valid HTML found in AI response')
    return false
  }

  // Validate HTML
  if (!isValidHTML(html)) {
    console.error('Invalid HTML structure')
    return false
  }

  // Sanitize HTML
  const sanitizedHTML = sanitizeHTML(html)

  try {
    if (selectedComponent) {
      // Replace the selected component
      const wrapper = editor.DomComponents.getWrapper()
      const targetComponent = wrapper?.find(`#${selectedComponent.id}`)?.[0]

      if (targetComponent) {
        // Replace component content
        targetComponent.components(sanitizedHTML)
      } else {
        console.warn('Selected component not found, appending to canvas')
        editor.addComponents(sanitizedHTML)
      }
    } else {
      // Replace entire canvas content for full page generation
      editor.setComponents(sanitizedHTML)
    }

    return true
  } catch (error) {
    console.error('Error injecting HTML to editor:', error)
    return false
  }
}

/**
 * Append HTML to the end of the canvas
 */
export function appendHTMLToEditor(
  editor: Editor | null,
  aiResponse: string
): boolean {
  if (!editor) {
    console.error('Editor is not initialized')
    return false
  }

  const html = extractHTML(aiResponse)

  if (!html) {
    console.error('No valid HTML found in AI response')
    return false
  }

  if (!isValidHTML(html)) {
    console.error('Invalid HTML structure')
    return false
  }

  const sanitizedHTML = sanitizeHTML(html)

  try {
    editor.addComponents(sanitizedHTML)
    return true
  } catch (error) {
    console.error('Error appending HTML to editor:', error)
    return false
  }
}
